$font-small: 13px;
$color-grey: grey;
$boxShadow: 0 0 20px rgb(220, 220, 220);

a {
  text-decoration: none;
  color: inherit;
}
a:hover {
  text-decoration: none;
  color: inherit;
}

// HOME
.home {
  .jumbotron {
    background-image: url(./img/장미.jpg);
    background-size: cover;
    margin: 50px 0;
    padding: 30px;
  }
}

// IMAGEPOST
.image-upload {
  margin: 30px;
  text-align: center;
  h4 {
    margin-bottom: 20px;
  }
  .form {
    box-shadow: $boxShadow;
    padding: 20px;
    border-radius: 10px;
    form {
      .input {
        input {
          width: 100%;
          border: none;
        }
      }
      .btn {
        margin-top: 20px;
        display: block;
      }
    }
  }
}

// POST
.upload {
  margin: 30px;
  text-align: center;
  h4 {
    margin-bottom: 20px;
  }
  .form {
    box-shadow: $boxShadow;
    padding: 20px;
    border-radius: 10px;
    form {
      .input {
        input {
          width: 100%;
          border: none;
        }
        input:focus {
          outline: none;
        }
        input::placeholder {
          text-align: center;
        }
        textarea {
          width: 100%;
          border: none;
          resize: none;
        }
        textarea:focus {
          outline: none;
        }
        textarea::placeholder {
          text-align: center;
        }
        select {
          border: none;
          width: 200px;
          text-align: center;
        }
        select:focus {
          outline: none;
        }
        option {
          border: none;
        }
        hr {
          width: 100%;
          margin: 20px auto;
        }
      }
      .btn {
        margin-top: 20px;
      }
    }
  }
}

// LOGIN
.login {
  .jumbotron {
    background-image: url(./img/장미.jpg);
    background-size: cover;
    margin: 50px 0;
    padding: 30px;
  }
  form {
    margin-left: 30px;
    .text {
      margin: 20px;
      input {
        display: block;
        margin-top: 5px;
      }
    }
    .cb {
      margin: 0 0 5px 20px;
      input {
        margin-right: 10px;
      }
    }
    button {
      margin: 20px;
    }
  }
}

// JOIN
.join {
  .jumbotron {
    background-image: url(./img/장미.jpg);
    background-size: cover;
    margin: 50px 0;
    padding: 30px;
  }
  form {
    margin-left: 30px;
    .text {
      margin: 20px;
      input {
        display: block;
        margin-top: 5px;
      }
    }
    .cb {
      margin: 0 0 5px 20px;
      input {
        margin-right: 10px;
      }
    }
    button {
      margin: 20px;
    }
  }
}

// PRODUCTS
.products {
  .list-group {
    margin: 20px;
    .item {
      display: flex;
      width: 100%;
      margin-bottom: 20px;
      img {
        width: 200px;
        border-radius: 10px;
      }
      .info {
        padding: 20px;
        div {
          padding-bottom: 5px;
        }
        .address {
          font-size: $font-small;
          color: $color-grey;
        }
        .price {
          font-weight: bold;
        }
      }
    }
  }
  .btn {
    position: fixed;
    bottom: 20px;
    right: 20px;
  }
}

// CHATROOM
.chatroom {
  .chat-content {
    height: 450px;
    overflow-y: scroll;
    padding: 10px;
    li {
      margin-top: 10px;
      list-style: none;
    }
  }

  .text-small {
    font-size: 12px;
    color: gray;
    margin-bottom: 0;
  }
  .chat-box {
    background: #eee;
    padding: 5px;
    border-radius: 5px;
    float: left;
  }
  .mine {
    float: right;
  }
}
